* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 19px;
  margin: 0;
  padding: 0;
}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: #2c3e50;
  min-height: 100vh;
}

.browser {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background: #7f8c8d;
  border-radius: 5px;
  -webkit-box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
          box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
  min-width: 80%;
  min-height: 50vh;
}

.tabs {
  -ms-flex-negative: 1;
      flex-shrink: 1;
  padding: 10px 10px 0;
  display: -ms-flexbox;
  display: flex;
}

.tab {
  position: relative;
  background: hsl(204, 64%, 44%);
  min-width: 120px;
  font-size: 14px;
  border-radius: 8px 8px 0 0;
  -webkit-transition: 0.3s ease all;
  -o-transition: 0.3s ease all;
  transition: 0.3s ease all;
  overflow: hidden;
  cursor: pointer;
}

.tab a {
  display: block;
  padding: 12px 25px;
  color: #ecf0f1;
  text-decoration: none;
}

.tab a.is-active {
  background: hsl(204, 64%, 35%);
  color: #eee;
}

.address-bar {
  background: #5533b3;
  padding: 8px;
}

.address-bar input {
  background: #3a2081;
  color: #eee;
  border-radius: 20px;
  border: none;
  outline: none;
  width: 100%;
  padding: 8px 20px;
  font-size: 18px;
}

.viewport {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: #bdc3c7;
  color: #2980b9;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-positive: 1;
      flex-grow: 1;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.highlight {
  position: absolute;
  height: 100px;
  width: 240px;

  /* background-image: linear-gradient(
    to right,
    #003973 0%,
    #e5e5be 51%,
    #003973 100%
  ); */

  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(#757f9a),
    color-stop(51%, #d7dde8),
    to(#757f9a)
  );

  background-image: -webkit-linear-gradient(
    left,
    #757f9a 0%,
    #d7dde8 51%,
    #757f9a 100%
  );

  background-image: -o-linear-gradient(
    left,
    #757f9a 0%,
    #d7dde8 51%,
    #757f9a 100%
  );

  background-image: linear-gradient(
    to right,
    #757f9a 0%,
    #d7dde8 51%,
    #757f9a 100%
  );

  border-radius: 50px;
  opacity: 0.6;
  /* important code, only A tag behind highlight is clickable */
  pointer-events: none;
  -webkit-transition: all 0.1s ease;
  -o-transition: all 0.1s ease;
  transition: all 0.1s ease;
}



